<template>
    <div>
        <u-button width="80px" height="30px" @on-click="onClicked">点击切图</u-button>
        <u-button width="80px" height="30px" @on-click="onGridClicked">加载9宫图</u-button>
        <img :src="btnImg" />
        <img :src="gridImg" />
    </div>
</template>
<script>
    import img from './img/barragefullscreen_btn.png';
    import imgtoa from './img/img_tip_bg.png';
    export default {
        data () {
            return {
                btnImg: img,
                gridImg: imgtoa,
                index: 0
            };
        },
        methods: {
            onClicked () {
                this.index = (++this.index) % 2;
                var texture = this.$Core.App.assets().getTexture(img);
                texture && texture.on(this.$Core.Texture.ONUPDATED, () => {
                    this.onClipsLoaded(texture);
                });
                this.onClipsLoaded(texture);
            },
            onGridClicked () {
                var texture = this.$Core.App.assets().getTexture(imgtoa);
                texture && texture.on(this.$Core.Texture.ONUPDATED, () => {
                    this.onGridLoaded(texture);
                });
                this.onGridLoaded(texture);
            },
            onClipsLoaded(texture) {
                if (!texture.valid) {
                    return;
                }
                const clips = (texture && texture.toClips(3, 1, 1)) || [];
                texture = (clips.length > this.index) && clips[this.index] || null;
                texture && texture.on(this.$Core.Texture.ONUPDATED, () => {
                    this.btnImg = (texture && texture.dataURL) || '';
                });
                this.btnImg = (texture && texture.dataURL) || '';
            },
            onGridLoaded(texture) {
                if (!texture.valid) {
                    return;
                }
                const gridTexture = (texture && texture.toGrid(10, 10, 78, 15, 200, 100));
                gridTexture && gridTexture.on(this.$Core.Texture.ONUPDATED, () => {
                    this.gridImg = (gridTexture && gridTexture.dataURL) || '';
                });
                this.gridImg = (gridTexture && gridTexture.dataURL) || '';
            }
        }
    };
</script>
<style>
</style>



